// import React, { useEffect } from "react";
import React from "react";
import { Link, useLocation, useHistory } from "react-router-dom";

const Navbar = () => {
    let history = useHistory();
    const location = useLocation();
    //   useEffect(() => {
    //     console.log(location.pathname);
    //   }, [location]);

    const handleLogout = () => {
        localStorage.removeItem('token');
        history.push("/login")
    }

    return (
        <>
            <nav className="navbar navbar-expand-lg navbar-dark bg-dark">
                <div className="container-fluid">
                    <Link className="navbar-brand" to="/">
                        iNotebook
                    </Link>
                    <button
                        className="navbar-toggler"
                        type="button"
                        data-bs-toggle="collapse"
                        data-bs-target="#navbarSupportedContent"
                        aria-controls="navbarSupportedContent"
                        aria-expanded="false"
                        aria-label="Toggle navigation"
                    >
                        <span className="navbar-toggler-icon"></span>
                    </button>
                    <div className="collapse navbar-collapse" id="navbarSupportedContent">
                        <ul className="navbar-nav me-auto mb-2 mb-lg-0">
                            <li className="nav-item">
                                <Link className={`nav-link ${location.pathname === "/" ? "active" : ""}`} aria-current="page" to="/">
                                    Home
                                </Link>
                            </li>
                            <li className="nav-item">
                                <Link className={`nav-link ${location.pathname === "/about" ? "active" : ""}`} to="/about">
                                    About
                                </Link>
                            </li>
                        </ul>
                        {localStorage.getItem('token') ? <button className="btn btn-sm btn-dark" onClick={handleLogout}>Logout</button> : <form className="d-flex">
                            <Link className="btn-sm btn-dark mx-2" to="/login" role="button" style={{ "textDecoration": "none" }}>Login</Link>
                            <Link className="btn-sm btn-dark mx-2" to="/signup" role="button" style={{ "textDecoration": "none" }}>SignUp</Link>
                        </form>}
                    </div>
                </div>
            </nav >
        </>
    );
};

export default Navbar;
